section {
	font-family: jf;
	padding-top: 80px;

	div.ads {
		div.elementscontainer {
			figure {
				width: 70%;
				height: 150px;
				margin: 20px 10px;
				display: flex;
				align-items: stretch;

				div.strip {
					width: 30px;
					background-color: #ed7e6a;
				}

				div.figure {
					width: 100%;
					background: linear-gradient(90deg, #e3e3e4 0%, #ffffff 100%);
				}
			}
		}
	}

	article {
		color: white;
		padding: 0 10px 40px;
		background-color: #f3f3f4;

		div.right {
			div.line {
				border: 1px solid #cbcbcd;
				width: 95%;
				margin: 10px auto;
			}

			width: 70%;

			div.upper {
				width: 100%;
				padding-top: 30px;

				div.text {
					height: 100px;
					background-color: white;
					border: 2px solid #bdbec0;
					border-radius: 20px;
					padding: 10px;
				}

				div.line{
					margin: 0 auto;
				}

				div.icons {
					padding: 10px;
					height: 100px;
					display: flex;
					justify-content: space-between;

					div.icon {
						height: 100%;
						width: 15%;
						border-radius: 10px;
						padding: 3px;

						div.inner {
							border-radius: 10px;
							border: 2px dashed white;
							display: flex;
							justify-content: space-around;
							align-items: center;
							width: 100%;
							height: 100%;
						}

						&.green {
							background-color: #8dc540;
						}

						&.voilet {
							background-color: #91278c;
						}

						&.blue {
							background-color: #179dda;
						}

						&.orange {
							background-color: #fdc027;
						}

						&.pink {
							background-color: #ee237c;
						}
					}
				}
			}

			div.lower {
				div.text {
					margin-top: 60px;
					position: relative;

					&:nth-child(2n){
						div.title{
						right: 120px;
						}
					}

					div.title {
						z-index: 0;
						top: -45px;
						right: 5px;
						position: absolute;
						color: white;
						display: inline-block;
						padding: 5px;
						border-radius: 10px;

						div.inner {
							border: 2px dashed white;
							width: 100%;
							height: 100%;
							border-radius: 10px;
							padding: 5px 15px;

							p {
								line-height: 1.5em;
							}
						}
						&.green {
							background-color: #8dc540;
						}

						&.voilet {
							background-color: #91278c;
						}

						&.blue {
							background-color: #179dda;
						}

						&.orange {
							background-color: #fdc027;
						}

						&.pink {
							background-color: #ee237c;
						}
					}

					div.content {
						position: relative;
						z-index: 1;
						color: #5b5b5b;
						background-color: #FFF;
						border: 2px solid #bdbec0;
						border-radius: 10px;
						padding: 10px;

						p {
							font-size: 0.8em;
							text-align: justify;
							line-height: 1.5em;
						}
					}
				}
			}
		}

		div.left {}
	}
}